@mixin boton($bgcolor){
    background-color: rgb(32, 144, 179);
    color: #fff;
    display: block;
    width: 50%;
    margin: auto;
    text-align: center;
    text-decoration: none;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    padding: 1rem 0;
    margin-top: 2rem;

}